  padding: 10px 10px 10px 11px;
  color: #c0bbb7;
  display: block;
  font-weight: normal;
  text-decoration: none !important;
  position: relative; }

/* Second level */
nav > ul > li > ul > li > a {
  padding-top: 7px;
  padding-bottom: 7px; }

/* Third Level */
nav > ul > li > ul > li > ul > li > a {
  padding-top: 7px;
  padding-bottom: 7px; }

