  opacity: .5;
  cursor: not-allowed;
  color: #c0bbb7; }

/* All levels */
nav ul b {
  float: right;
  font-size: 11px;
  margin-top: 2px; }

/* All levels */
nav ul span.menu-item-parent {
  display: inline-block;
  margin: 0;
  padding: 0; }

/* First level: A, Second level: LI Open */
nav ul li.open > a, nav ul li.open > a b {
  color: #fff !important; }

/* First level: Icon */
nav > ul > li > a b {
  position: absolute !important;
  right: 10px;
  top: 10px; }

nav > ul > li > a > i {
  /*font-size: 121%; fa-lg overrides this so..  */
